@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,700);
@use 'fonts.scss';body{font-family:'Open Sans';font-weight:400}*,*::before,*::after{box-sizing:border-box}html{font-family:sans-serif;line-height:1.15;-webkit-text-size-adjust:100%;-ms-text-size-adjust:100%;-ms-overflow-style:scrollbar;-webkit-tap-highlight-color:rgba(0,0,0,0);-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}body{font-family:Helvetica,Arial,sans-serif;font-size:16px;color:#333;background-color:#fff;-webkit-overflow-scrolling:touch}body.is-fixed{position:fixed;top:0;left:0;right:0;overflow:hidden}body.modal-is-open{position:fixed;top:0;left:0;width:100%;height:100vh;overflow:hidden}a{background-color:rgba(0,0,0,0);-webkit-text-decoration-skip:objects;text-decoration:none;color:#333}a,a:hover,a:focus,a:active{outline:none}a,area,button,[role="button"],input:not([type="range"]),label,select,summary,textarea{touch-action:manipulation}img{vertical-align:middle;border-style:none}img.object-fit{object-fit:cover;object-position:center}img.ir{display:block;max-width:100%;height:auto}a,button,html [type="button"],[type="reset"],[type="submit"],[role=button],[role=link]{appearance:none;-webkit-appearance:none;box-shadow:none;outline:none !important}ul{list-style:none;margin:0;padding:0}.app{height:calc(100vh - 50px);overflow:hidden}

